<template>
   <section class="signInBackground">
    <div class="bg"></div>

    <div class="pa-8">
      <div style="height: 200px"></div>
      <v-row class="">
        <v-col class="d-flex flex-column align-center justify-center">
          <p class="middleFont">'    '</p>
          <div class="d-flex align-center">
            <p class="green--text" style="font-size: 40px"></p>
            <p class="" style="font-size: 30px">(Eat)</p>
            <p class="green--text" style="font-size: 40px"></p>
          </div>
        </v-col>
      </v-row>
      <v-divider></v-divider>

      <v-row>
        <v-col class="d-flex justify-center">
          <v-form
            style="width: 20%"
            ref="form"
            v-on:submit.prevent="signUp"
            v-model="valid"
            lazy-validation
          >
            <v-text-field
              placeholder="  ."
              v-model="email"
              :rules="emailRules"
              label=""
              required
            ></v-text-field>

            <v-text-field
              placeholder="  ."
              v-model="password"
              :rules="passwordRules"
              label=""
              required
            ></v-text-field>

            <v-text-field
              placeholder=" ."
              v-model="name"
              :rules="passwordRules"
              label=""
              required
            ></v-text-field>

            <v-text-field
              placeholder="  ."
              :rules="passwordRules"
              label=" "
              required
            ></v-text-field>

            <v-text-field
              placeholder=" ."
              :rules="passwordRules"
              label=""
              required
            ></v-text-field>
            
            <v-text-field
              placeholder="  ."
              :rules="passwordRules"
              label=""
              required
            ></v-text-field>

            <v-checkbox
              v-model="checkbox"
              :rules="[
                (v) =>
                  !!v || '   ,    .',
              ]"
              label="  ?"
              required
            ></v-checkbox>
            <div>
              <v-btn
                :disabled="!valid"
                color="#9ccc65"
                class="mr-4"
                type="submit"
                @click="validate"
                >
              </v-btn>

              <v-btn color="#B0BEC5" class="mr-4" @click="reset"></v-btn>

              <v-btn color="#B0BEC5" class="mr-4" link: to="/"
                ></v-btn
              >
            </div>
          </v-form>
        </v-col>
      </v-row>
    </div>
  </section>

</template>

<script>
import axios from "axios";
import router from "@/router";

export default {

  data() {
    return {
      valid: true,
      passwordRules: [(v) => !!v || "   !"],
      //  
      emailRules: [
        (v) => !!v || "   .",
        (v) => /.+@.+/.test(v) || "    .",
      ],
      checkbox: false,
      email: "",
      password: "",
      name: ""
    };
  },

  methods: {

    // 
    async signUp() {

        var vm = this;

      await axios({
        url: "/signUp",
        method: "POST",
        data: {
          name: this.name,
          email: this.email,
          password: this.password,
        },
      })
        .then(function (response) {
          if (response.data != 0) {
            // store Vuex userId 
            alert(" .");
        vm.$store.commit("setUserId", response.data); //store.userId 
        vm.saveSession();
            console.log("  userId: " + response.data);
            router.push("/dashBoard");
          } else if (response.data == 0) {
            alert(
              " .    ."
            );
          }
        })
        .catch(function (err) {
          console.log("axios " + err);
        });
      (this.email = ""), (this.password = ""), (this.name = ""), this.reset();
    },


      // userId   
      saveSession() {
      sessionStorage.setItem("loginedId",String(this.$store.state.userId));
    },

  
    validate() {
      this.$refs.form.validate();
    },

    reset() {
      this.$refs.form.reset();
    },
  },

};
</script>

